import { Basics } from "../contents/biography";

export const NetworkLinks = ({
  personalInformation,
}: {
  personalInformation: Basics;
}) => {
  const website = personalInformation.website && (
    <p className="ui no-bottom">
      <a
        href={personalInformation.website}
        target="_blank"
        rel="noreferrer"
        style={{ display: "flex" }}
      >
        <i className="large home icon "></i>
        {personalInformation.website.replace("https://", "")}
      </a>
    </p>
  );

  const email = personalInformation.email && (
    <p className="ui no-bottom">
      <a
        href={`mailto:${personalInformation.email}`}
        target="_blank"
        rel="noreferrer"
        style={{ display: "flex" }}
      >
        <i className="large mail outline icon "></i>
        {personalInformation.email}
      </a>
    </p>
  );

  const profiles = personalInformation.profiles.map(
    ({ url, network, username }) => (
      <p key={url} className="ui no-bottom">
        <a
          href={url}
          target="_blank"
          rel="noreferrer"
          style={{ display: "flex" }}
        >
          <i className={`large ${network.toLowerCase()} icon `}></i>
          {username}
        </a>
      </p>
    )
  );

  return (
    <>
      {website}
      {email}
      {profiles}
    </>
  );
};
