import { Profile } from "../contents/biography";

export const SocialProfiles = ({
  profiles,
  email,
}: {
  profiles: Profile[];
  email: string;
}) => (
  <>
    {profiles.map(({ url, network }) => (
      <a key={url} href={url} target="_blank" rel="noreferrer">
        <i className={`big ${network.toLowerCase()} icon icon-color`}></i>
      </a>
    ))}
    {email && (
      <a href={`mailto:${email}`}>
        <i className="big mail square icon icon-color"></i>
      </a>
    )}
  </>
);
