export const Skills = ({ skills }: { skills: Skill[] }) => {
  return (
    skills && (
      <motion.div variants={childVariant}>
        <div className="ui horizontal divider">
          <h3 className="ui blue left aligned medium header">
            <i className="code branch icon"></i>
            Skills
          </h3>
        </div>

        <div className="ui paragraph">
          {skills.map(({ name, keywords }) => (
            <p key={name} className="ui no-bottom">
              <b>{name}: </b> {keywords.join(", ")}
            </p>
          ))}
        </div>
      </motion.div>
    )
  );
};
