import { NavLink, useLocation } from "react-router-dom";
import { Basics } from "../contents/biography";

const linkClassName = ({ isActive }: { isActive: boolean }) =>
  `${isActive ? "active" : ""} item bold flex`;

export const Menubar = ({
  personalInformation,
  numberOfProjects,
  numberOfEssays,
}: {
  personalInformation: Basics;
  numberOfProjects: number;
  numberOfEssays: number;
}) => {
  const { pathname } = useLocation();
  let content = null;

  if (pathname !== "/")
    content = (
      <div className="nonprintable">
        <nav className="ui blue stackable secondary pointing menu">
          <NavLink
            to="/"
            className={({ isActive }) =>
              `${isActive ? "active" : ""} brand item`
            }
            style={{ height: 47 }}
          >
            <img
              className="ui avatar image"
              src={personalInformation.picture}
              alt="personal"
            />
            <span className="bold">{personalInformation.name}</span>
          </NavLink>
          <NavLink to="/projects" className={linkClassName}>
            <i className="large briefcase icon"></i>
            Projects ({numberOfProjects})
          </NavLink>
          <NavLink to="/essays" className={linkClassName}>
            <i className="large pencil icon"></i>
            Essays ({numberOfEssays})
          </NavLink>
          <NavLink to="/resume" className={linkClassName}>
            <i className="large book icon"></i>
            Resume
          </NavLink>
        </nav>
      </div>
    );

  return content;
};
