import { Basics } from "../contents/biography";

export const Address = ({
  personalInformation,
}: {
  personalInformation: Basics;
}) => {
  const { location, phone } = personalInformation;
  const { address, city, region, postalCode, countryCode } = location;
  return (
    <>
      <p className="ui no-bottom">{address}</p>
      <a
        href={`http://maps.google.com/?q=${region}, ${city}`}
        target="_blank"
        rel="noreferrer"
        className="ui no-bottom"
        style={{ display: "flex", justifyContent: "flex-end" }}
      >
        <i className={`large map marker icon `}></i>
        {city}, {region} {postalCode} {countryCode}
      </a>
      <a
        href={`tel:${phone}`}
        target="_blank"
        rel="noreferrer"
        className="ui no-bottom"
        style={{ display: "flex", justifyContent: "flex-end" }}
      >
        <i className={`large phone icon `}></i>
        {phone}
      </a>
    </>
  );
};
