import { motion } from "framer-motion";
import { buildChildVariant } from "../animation";
import { Work as WorkInterface } from "../contents/biography";

const childVariant = buildChildVariant();

const formatDate = (date: string) =>
  new Date(date).toLocaleString("default", {
    month: "short",
    year: "numeric",
  });

export const Work = ({ work }: { work: WorkInterface[] }) => {
  return (
    <motion.div variants={childVariant} style={{ marginTop: 10 }}>
      <div className="ui horizontal divider">
        <h3 className="ui blue left aligned header">
          <i className="desktop icon"></i>
          Work
        </h3>
      </div>

      <div className="fourteen wide column paragraph">
        {work.map(
          ({
            position,
            company,
            website,
            summary,
            highlights,
            startDate,
            endDate,
          }) => (
            <div key={summary} className="ui grid">
              <div className="twelve wide column no-bottom">
                <p className="ui no-bottom">
                  <b>
                    {position}, {company}
                  </b>
                </p>
                <p className="ui no-bottom">
                  <a href={website}>{website.replace("https://", "")}</a>
                </p>
                <p
                  className="ui no-bottom"
                  dangerouslySetInnerHTML={{ __html: summary }}
                />
                <ul style={{ margin: 0 }}>
                  {highlights.map((highlight) => (
                    <li key={highlight}>{highlight}</li>
                  ))}
                </ul>
                <ul></ul>
              </div>
              <div className="four wide right aligned column no-bottom">
                <p className="ui no-bottom">
                  <b>
                    {startDate && formatDate(startDate)}
                    {startDate !== "" && endDate !== "" && " - "}
                    {endDate && endDate !== "Present" && formatDate(endDate)}
                    {endDate === "Present" && "Present"}
                  </b>
                </p>
              </div>
            </div>
          )
        )}
      </div>
    </motion.div>
  );
};
