import { Home } from "./routes/Home";
import { bio } from "./contents/biography";
import { Route, Routes } from "react-router-dom";
import { Projects } from "./routes/Projects";
import { projects } from "./contents/projects";
import { Menubar } from "./layouts/Menubar";
import { Resume } from "./routes/Resume";
import { essays } from "./contents/essays";
import { BookShelf } from "./pages/projects/BookShelf";
import { NutritionTracker } from "./pages/projects/NutritionTracker";

function App() {
  return (
    <>
      <Menubar
        personalInformation={bio.basics}
        numberOfProjects={projects.length}
        numberOfEssays={essays.length}
      />
      <Routes>
        <Route
          path="/"
          element={
            <Home
              personalInformation={bio.basics}
              interests={bio.interests}
              numberOfProjects={projects.length}
            />
          }
        />
        <Route path="/projects" element={<Projects projects={projects} />} />
        <Route path="/projects/book-shelf" element={<BookShelf />} />
        <Route
          path="/projects/nutrition-tracker"
          element={<NutritionTracker />}
        />
        <Route
          path="/resume"
          element={<Resume personalInformation={bio.basics} {...bio} />}
        />
      </Routes>
    </>
  );
}

export default App;
