export const buildContainerVariant = (
  delay: number = 0.3,
  staggerChildren: number = 0.3
) => ({
  hidden: {
    opacity: 0,
  },
  visible: {
    opacity: 1,
    transition: {
      delay,
      staggerChildren,
      when: "beforeChildren",
    },
  },
});

export const buildChildVariant = () => ({
  hidden: {
    opacity: 0,
  },
  visible: {
    opacity: 1,
  },
});
