import { motion } from "framer-motion";
import { buildChildVariant } from "../animation";
import { Interest } from "../contents/biography";

const childVariant = buildChildVariant();

export const Interests = ({ interests }: { interests: Interest[] }) => {
  return (
    interests && (
      <motion.div variants={childVariant}>
        <div className="ui horizontal black divider">
          <h3 className="ui blue left aligned medium header">
            <i className="paint brush icon"></i>
            Interests
          </h3>
        </div>
        <p className="ui paragraph">
          {interests
            .reduce(
              (current, { name, keywords }) =>
                current +
                name +
                (keywords ? ` (${keywords.join(", ")}), ` : ""),
              ""
            )
            .slice(0, -2)}
        </p>
      </motion.div>
    )
  );
};
