import { motion } from "framer-motion";
import { useNavigate } from "react-router-dom";
import { buildChildVariant } from "../animation";
import { Project } from "../contents/projects";

const childVariant = buildChildVariant();

export const ProjectCard = (project: Project) => {
  const navigate = useNavigate();

  const handleCardClick = () => {
    navigate(project.path);
  };

  return (
    <motion.div
      variants={childVariant}
      className="card hover-pointer"
      onClick={handleCardClick}
    >
      <div className="image" style={{ padding: "10px" }}>
        <img src={project.image} alt={project.title} />
      </div>
      <div className="content">
        <span className="header"> {project.title}</span>
        <div className="meta">
          <span className="date bold"> {project.completionDate} </span>
        </div>

        <div className="description bold">{project.summary}</div>
      </div>

      <div className="extra content">
        <div className="ui labels">
          {project.labels.map((label) => (
            <div key={label} className="ui basic label">
              {label}
            </div>
          ))}
        </div>
      </div>
    </motion.div>
  );
};
