import { motion } from "framer-motion";
import { buildChildVariant } from "../animation";
import { Education as EducationInterface } from "../contents/biography";

const childVariant = buildChildVariant();

const formatDate = (date: string) =>
  new Date(date).toLocaleString("default", {
    month: "short",
    year: "numeric",
  });

export const Education = ({
  education,
}: {
  education: EducationInterface[];
}) => {
  return (
    <motion.div variants={childVariant}>
      <div className="ui horizontal divider">
        <h3 className="ui blue left aligned medium header">
          <i className="university icon"></i>
          Education
        </h3>
      </div>

      <div className="fourteen wide column no-bottom paragraph">
        {education.map(
          ({ institution, studyType, area, courses, startDate, endDate }) => (
            <div key={area} className="ui grid">
              <div className="twelve wide column no-bottom">
                <p className="ui no-bottom">
                  <b>{institution}</b>
                </p>
                <p className="ui no-bottom">
                  {studyType}, {area}
                </p>
                <ul style={{ margin: 0 }}>
                  {courses.map((course) => (
                    <li key={course}>{course}</li>
                  ))}
                </ul>
              </div>
              <div className="four wide right aligned column no-bottom">
                <p className="ui no-bottom">
                  <b>
                    {startDate && formatDate(startDate)}
                    {startDate !== "" && endDate !== "" && " - "}
                    {endDate && endDate !== "Present" && formatDate(endDate)}
                    {endDate === "Present" && "Present"}
                  </b>
                </p>
              </div>
            </div>
          )
        )}
      </div>
    </motion.div>
  );
};
